import React from "react";
import { Wrapper, Tittle } from "./styles";
import CountDown from "../countdown";
import Maps from "../maps";
import Heart from "../hearts";
import CardMempelai from "../card-mempelai";
import InstagramLive from "../instagram-live";

function index() {
  return (
    <Wrapper>
      <div className="section-mempelai">
        <div className="mempelai-pria">
          <Tittle>Laki - Laki</Tittle>
          <CardMempelai
            nama={"Naruto Nama Lengkap"}
            type={"Putra"}
            bapak={"Sasuke"}
            ibu={"Sakura"}
            img={
              "data:image/jpeg;base64,"
            }
          />
        </div>
        <div className="lambang-cinta">
          <Heart></Heart>
        </div>
        <div className="mempelai-wanita">
          <Tittle>Perempuan</Tittle>
          <CardMempelai
            type={"Putri"}
            bapak={"Sasuke"}
            nama={"Sakura Nama Lengkap"}
            ibu={"Sakura"}
            img={
              "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcS-4fbKu5iB1XX8aKsuxmQPOCQD6kvn3IGDsw&usqp=CAU"
            }
          />
        </div>
      </div>

      <CountDown />
      <InstagramLive />
      <Maps />
    </Wrapper>
  );
}

export default index;
